*{
    margin:0;
    padding:0;

}

.menuP{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding:0.8cm;
    top:0;
    overflow:hidden;
    background-color: rgba(0, 0, 0, 0.448);
    color: white;
    position:sticky;

}

.itemP{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    column-gap: 0.5cm;
}